.Screen {

    width: 100%;
    height: 25%;
    padding: 1rem 2rem; 
    background-color: var(--color-black);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: end;
        align-items: flex-end;
}

.ResultDisplay{
    display: inline-block;
    color: var(--color-white);

    font-size: 4rem;
    font-weight: 300;

    -webkit-transition: all .3s;

    -o-transition: all .3s;

    transition: all .3s;
}

.SubResult{
    color: var(--color-yellow);
    font-size: 2rem;
    font-weight: 500;
    
    -webkit-transition: all .3s;
    
    -o-transition: all .3s;
    
    transition: all .3s;
}


